.baseManage{
    .loginPage{
        .sysName{
            font-size: 73px;
            font-weight: bolder;
            text-align: center;
            left: 0;
            position: fixed;
            top: calc(38% - 291px);
            right: 0;
        }
        width: 600px;
        position: absolute;
        top: calc(38% - 100px);
        left: calc(50% - 388px);
    }

    .mapDiv{
        position: absolute;
        top: 0;
        bottom: 0; 
        right: 0;
    }
    .leftMenu{
        overflow:auto;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        background: #f5f3f0; 
        border-right: solid 3px #656565;
        padding-left: 5px;

        .title{
            font-size: 21px;
            background: #6b8ee9;
            height: 35px;
            line-height: 35px;
            color: #f5f3f0;
            font-weight: bolder;

            .loginInfo{
                font-size: 15px;
                font-weight: 100;
                .logoutBtn{
                    margin-left: 5px;
                    color: red;
                    font-size: 12px;
                    font-weight: 100;
                    cursor: pointer;
                    &:hover{
                        text-decoration: underline;
                    }
                }
            }
        }
    }

    .dataReport{
        width: 100%;
        .echarts-div{
            width: 100%;
            height: 300px;
        }
    }
}